<template>
  <div>
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
        <a class="icon icon_manue"> manue </a>
      </div>
      <h1>Refund</h1>
      <div class="chartWrap">
          <span class="bag" v-if="indexData.shoppingCartNum>0">
          <em class="font10"> {{ indexData.shoppingCartNum }} </em>
        </span>
        <a class="iconfont cart" @click="$router.push('/cart')"> &#xe669; </a></div>
    </section>

    <section class="exchangeContent">
      <div class="blocks">
        <div class="proWrap clearfix">
          <div class="imgWrap">
            <img :src="productDetail.img0" class="img">
          </div>
          <div class="numRight">
            <span class="iconfont"> &#xe619; </span>
            <span> {{productDetail.productQuantity}} </span>
          </div>
          <div class="rightMain">
            <p class="name"> {{productDetail.productName}} </p>
            <p class="size"> {{getAttributes(productDetail.attrOptList)}} </p>
          </div>
        </div>
        <div class="commonRow">
          <p class="tittle"> Application Qty: </p>
          <div class="gw_num clearfix">
           <van-stepper v-model="productQuantity" :max="productDetail.productQuantity*1" />
          </div>
        </div>
        <div class="commonRow" v-if="false">
          <p class="tittle"> Refund Amount: </p>
          <div class="price">
              {{productDetail.productAmount}}AED
          </div>
        </div>
      </div>

      <div class="blocks mt10">
        <div class="commonRow" @click="showReason">
          <p class="tittle"> Select Reason: <sup class="require">*</sup> </p>
          <div class="rightCol">
           <span class="color999 single-ellipsis">  {{resonVal.name}} </span> <span class="iconfont arrowDown"> &#xe671; </span>
          </div>
        </div>
      </div>

      <div class="blocks mt10">
        <div class="commonRow" >
          <p class="tittle"> Problem Description <sup class="require">*</sup> </p>
        </div>
        <div class="txt">
          <textarea  placeholder="Min 20 Characters" v-model="reason">

          </textarea>
        </div>
      </div>

    </section>

    <div class="exchangeBtnwrap">
      <van-button size="large" type="danger" @click="apply"> Apply </van-button>
    </div>

    <van-popup v-model="reasonShow" position="bottom">
      <div class="resonList">
        <a class="close" @click="reasonShow=false"> Cancel </a>
        <ul class="clearfix">
          <li v-for="(item,index) in productDetail.refundReason" :key="index" :class="{'red':item.code==temp.code}"  @click="temp=item">
            <span class="iconfont radio"> {{item.code==temp.code?"&#xe63a;":"&#xe608;"}} </span>
            <div>
              <p class="name"> {{item.name}} </p>
            </div>
          </li>
        </ul>
        <div class="btnWrap mt20">
          <van-button type="danger"  @click="confirmReason" size="large">Confirm</van-button>
        </div>
      </div>
    </van-popup>


  </div>
</template>

<script>
import { orderDetail,goRefund,applyRefundProduct } from "@/api/order";
import { mapGetters } from "vuex";
export default {
  name: 'exchange',
  data () {
    return {
      resonVal:{
        "label":"",
        "value":0
      },
      temp:{},
      reasonList:[],
      productQuantity:1,
      reasonShow:false,
      orderNo:this.$route.query.orderNo,
      skuCode:this.$route.query.skuCode,
      productDetail:{},
      reason:""

    }
  },
  computed: {
    ...mapGetters([
      "indexData",
      "productData"
    ])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
      goRefund({ orderNo: this.orderNo,skuCode:this.skuCode }).then(res => {
        this.productDetail = res.data.result;
      });
    },
    showReason(){
      this.temp={
        "name":this.resonVal.name,
        "code":this.resonVal.code
      }
      this.reasonShow=true;
    },
    confirmReason(){
      this.resonVal={
        "name":this.temp.name,
        "code":this.temp.code
      }
      this.reasonShow=false;
    },
    goBack(){
      this.bus.$emit('goBack');
    },
    apply(){
      applyRefundProduct({
        orderNo:this.orderNo,
        skuCode:this.skuCode,
        refundReason:this.resonVal.code,
        remark:this.reason,
        productQuantity:this.productQuantity
      }).then(res=>{
        this.$router.go(-1);
      })
    },
     getAttributes(arr) {
      return !arr?'':arr.map(t => t.optionName).join(";");
    },


  }
}
</script>

<style lang="scss" scoped>
  @import "../common/css/orderList.scss";
  .close{font-size: 15px;}
</style>



